.section-3 {
  background-image: url(../img/bg-line.png);
  background-repeat: no-repeat;
  background-position: -10% 100%;
  padding-top: 120px;
  background-size: 1444px;
  padding-bottom: 120px;

  .together-photo {
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    top: 390px;
    z-index: -1;
  }

  h2 {
    margin-bottom: 550px;
  }

  h4 {
    font-size: 28px;
    font-weight: bold;
    line-height: 38px;
    margin-bottom: 50px;

    .h4 {
      margin-top: 20px;
      font-weight: 400;
      font-size: 20px;
      line-height: 32px;
    }
  }

  .end-dance {
    div:nth-child(1) {
      img {
        margin-left: -41px;
        margin-right: 40px;
      }

      margin-bottom: 30px;
    }

    div:nth-child(2) {
      img {
        margin-left: 40px;
      }
    }
  }
}